<template>
    <div>
        <div class="main-content__title"></div>
        <div class="project-path-info">http</div>
        <div class="rule-list-op">
            <div class="op">
                <input type="file" @change="fileUpload" class="importfile"/>
                <el-button size="small"></el-button>
            </div>
            <div class="op">
                <el-button size="small" @click='importRemoteRule'></el-button>
            </div>
            <div class="op">
                <el-button size="small" type="primary" @click='addRuleCollection'></el-button>
            </div>
        </div>

        <el-table border :data="$dc.ruleFileList">
            <el-table-column prop="name" label="" width="200">
                <template scope="scope">
                    {{ scope.row.name }}
                    <el-tag type="danger" v-if="scope.row.meta.remote" close-transition></el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="description" label=""/>
            <el-table-column label="" :width="230">
                <template scope="scope">
                    <div class="rule-file-op-container">
                        <el-button type="danger" icon='el-icon-delete' size="mini"
                                   @click='onDeleteFile(scope.row,scope.$index)'/>
                        <a :href="'#/editrule?name='+scope.row.name">
                            <el-button type="info" icon='el-icon-edit' size="mini">
                            </el-button>
                        </a>
                        <el-button type="info" icon='el-icon-download' size="mini"
                                   @click='onDownloadFile(scope.row, scope.$index)'/>
                        <span>
                            <el-button type="info" icon='el-icon-share' size="mini"
                                       @click='onShareFile(scope.row, scope.$index)'/>
                        </span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="" width="100">
                <template scope='scope'>
                    <el-switch
                            v-model="scope.row.checked"
                            :disabled="!$dc.ruleState"
                            @change="onSelectionChange(scope.row.name,scope.row.checked)"
                    />
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
  import copyToClipboard from 'copy-to-clipboard';
  import ruleApi from '../../../api/rule';
  import utilsApi from '../../../api/utils';
  import find from 'lodash/find';
  import './file-list.pcss';

  export default {
    name: 'rulefilelist',
    methods: {
      onDeleteFile(row, index) {
        this.$confirm(`: ${row.name}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ruleApi.deleteFile(row.name).then(response => {
            var serverData = response.data;
            if (serverData.code == 0) {
              this.$message({
                showClose: true,
                type: 'success',
                message: '!'
              });
            } else {
              this.$message.error(`${serverData.msg}`);
            }
          });
        });
      },
      onDownloadFile(row, index) {
        if (!row.meta.remote) {
          window.location = '/rule/download?name=' + row.name;
        } else {
          this.$alert(row.meta.url, 'URL', {
            confirmButtonText: 'OK',
            callback: action => {
            }
          });
        }
      },
      onShareFile(row, index) {
        let url = `https://${this.$dc.appInfo.pcIp}:${this.$dc.appInfo.webUiPort}/rule/file/raw?name=${encodeURIComponent(row.name)}`;
        // 
        copyToClipboard(url);
        this.$message(`${encodeURIComponent(row.name)}`);
      },
      // 
      async importRemoteRule() {
        let result = await this.$prompt(
          'url',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: ''
          }
        );

        let url = result.value;
        console.log(url);
        let response = await utilsApi.getRemoteFile(url);

        let remoteFileResponse = response.data;
        let content = remoteFileResponse.data;
        content.meta = {
          remote: true,
          url: url,
        };
        content.name = content.name + '-remote';
        content.checked = false;
        // 
        // 
        let varNameList = ruleApi.getReferenceVar(content);
        let infoStr;
        if (varNameList.length > 0) {
          infoStr = `${content.name},${varNameList.join(
            '; '
          )}`;
        } else {
          infoStr = `${content.name}`;
        }
        this.$alert(infoStr, '', {
          confirmButtonText: '',
          callback: action => {
            if (action == 'confirm') {
              // 
              ruleApi.saveFile(content.name, content).then(response => {
                var serverData = response.data;
                if (serverData.code == 0) {
                  // 
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                } else {
                  this.$message.error(`${serverData.msg}`);
                }
              });
            }
          }
        });
      },
      onSelectionChange(name, checked) {
        ruleApi.setFileCheckStatus(name, checked).then(response => {
          var serverData = response.data;
          if (serverData.code == 0) {
            this.$message({
              showClose: true,
              type: 'success',
              message: '!'
            });
          } else {
            this.$message.error(`${serverData.msg}`);
          }
        });
      },
      addRuleCollection() {
        this.$router.push('createrulefile');
      },
      fileUpload(evt) {
        var _this = this;
        var files = evt.target.files;
        var file = files[0];
        if (!file || file.type.indexOf('json') < 0) return;

        var reader = new FileReader();
        reader.onload = function (e) {
          var contentStr = e.target.result;
          var content = JSON.parse(contentStr);
          content.checked = false;
          // 
          var finded = find(_this.fileList, file => {
            return file.name == content.name;
          });
          if (finded) {
            _this.$message.error(
              `${
                content.name
                }name,`
            );
            return;
          }
          // 
          var varNameList = ruleApi.getReferenceVar(content);
          var infoStr;
          if (varNameList.length > 0) {
            infoStr = `${
              content.name
              },${varNameList.join(
              '; '
            )}`;
          } else {
            infoStr = `${content.name}`;
          }
          _this.$alert(infoStr, '', {
            confirmButtonText: '',
            callback: action => {
              if (action == 'confirm') {
                // 
                ruleApi.saveFile(content.name, content).then(response => {
                  var serverData = response.data;
                  if (serverData.code == 0) {
                    // 
                    _this.$message({
                      showClose: true,
                      message: '',
                      type: 'success'
                    });
                  } else {
                    _this.$message.error(`${serverData.msg}`);
                  }
                });
              }
            }
          });
        };
        reader.readAsText(file);
      }
    }
  };
</script>
<style>
    .addrule-btn-wrap {
        text-align: right;
    }


</style>



