<template>
    <div style="text-align: left">
        <!--  -->
        <div class="inline-block left-panel">
            <!--  -->
            <el-select v-model="action.type" placeholder=""
                       style="margin-top: 25px;margin-right: 10px;font-size: 12px"
                       size="small" :disabled="remote">
                <el-option v-for="item in ruleType" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div>
        <!-- -  -->
        <div v-if="action.type == 'redirect'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.target" size="small" :disabled="remote"
                          :placeholder="redireactPlaceholder">
                </el-input>
            </div>
        </div>
        <!-- -  el-selectbugel-selectv-show -->
        <div v-show="action.type == 'mockData'" class="inline-block right-panel">
            <div class="action-data">
              <span>
                <el-select v-model="action.data.dataId"
                           style="width: 300px" size="small" filterable placeholder="">
                  <el-option
                          v-for="dataentry in $dc.dataList"
                          :label="dataentry.name" :key="dataentry.id"
                          :value="dataentry.id">
                  </el-option>
                </el-select>
              </span>
              <span v-if="datafileEntry" style="margin-left: 10px;">
                <el-button type="text" @click="editDataFile(datafileEntry)">
                  
                </el-button>
              </span>
              <span style="margin-left: 10px;">
                <el-button type="text" @click="addDataFile"></el-button>
              </span>
            </div>
        </div>
        <!-- cookie -->
        <div v-if="action.type == 'addRequestCookie'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.cookieKey" size="small" :disabled="remote"
                          placeholder="cookie key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.cookieValue" size="small" :disabled="remote"
                          placeholder="cookie value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!--  -->
        <div v-if="action.type == 'modifyResponse'" class="inline-block right-panel">
            <div class="action-data">
                <el-select v-model="action.data.modifyResponseType" style="width: 260px"
                           size="small" placeholder="body" :disabled="remote">
                    <el-option v-for="item in modifyResponseType" :key="item.value" :label="item.label"
                               :value="item.value">
                    </el-option>
                </el-select>
                <span v-if="action.data.modifyResponseType == 'returnDataInJsonpStyle'">
                  <el-input v-model="action.data.callbackName" size="small" :disabled="remote" style="width:200px;"
                            placeholder="jsonp callback">
                  </el-input>
                </span>
            </div>
        </div>
        <!--  -->
        <div v-if="action.type == 'addRequestHeader'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.reqHeaderKey" size="small" :disabled="remote"
                          placeholder="header key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.reqHeaderValue" size="small" :disabled="remote"
                          placeholder="header value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!-- Query -->
        <div v-if="action.type == 'addQuery'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.queryKey" size="small" :disabled="remote"
                          placeholder="query key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.queryValue" size="small" :disabled="remote"
                          placeholder="query value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!--  -->
        <div v-if="action.type == 'addResponseHeader'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.resHeaderKey" size="small" :disabled="remote"
                          placeholder="header key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.resHeaderValue" size="small" :disabled="remote"
                          placeholder="header value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!--  -->
        <div v-if="action.type == 'scriptModifyRequest'" class="inline-block right-panel">
            <textarea style="width: 500px;height: 90px" v-model="action.data.modifyRequestScript">
            </textarea>
        </div>
        <!--  -->
        <div v-if="action.type == 'scriptModifyResponse'" class="inline-block right-panel">
            <textarea  style="width: 500px;height: 90px" v-model="action.data.modifyResponseScript">
            </textarea>
        </div>
    </div>
</template>

<script>
    import _ from 'lodash';
    export default {
        name: 'action-detail',
        props: ['action', 'remote'],
        data() {
            return {
                ruleType: [
                    { value: 'redirect', label: '' },
                    { value: 'mockData', label: '' },
                    { value: 'addRequestHeader', label: '' },
                    { value: 'addQuery', label: 'Query' },
                    { value: 'addResponseHeader', label: '' },
                    { value: 'modifyResponse', label: '' },
                    { value: 'addRequestCookie', label: 'cookie' },
                    { value: 'scriptModifyRequest', label: 'js' },
                    { value: 'scriptModifyResponse', label: 'js' }
                ],
                modifyResponseType: [
                    { value: 'addTimestampToJsCss', label: 'htmljscss' },
                    { value: 'returnDataInJsonpStyle', label: 'JSONP' },
                    { value: 'allowCros', label: '' },
                    { value: 'return404', label: '404' }
                ]
            };
        },
        methods: {
            addDataFile(){
                // 
                this.$dc.requestAddDataFile((id) => {
                    this.action.data.dataId = id;
                });
            },
            editDataFile(entry){
                this.$dc.requestEditDataFile(entry);
            }
        },
        computed: {
            datafileEntry(){
                if (this.action.type == "mockData") {
                    var finded = _.find(this.$dc.dataList, (entry) => {
                        return entry.id == this.action.data.dataId;
                    });
                    if (!finded) this.action.data.dataId = '';
                    return finded;
                }
            },
            redireactPlaceholder(){
                if (this.$dc.userId == 'root'){
                    return '(http/https)'
                } else {
                    return '(http/https)'
                }
            }
        }
    };

</script>
<style>
    .inline-block {
        display: inline-block;
    }

    .conditon {
        margin-top: 10px;
    }

    .conditon:after {
        content: "";
        display: block;
        border-bottom: 1px dotted #adadad;
    }

    .action {
        margin-bottom: 10px;
    }

    .left-panel {
        margin-top: 10px;
        height: 80px;
        width: 160px;
        vertical-align: top;
        border-right: 1px dotted #c0c0c0;
    }

    .right-panel {
        width: calc(100% - 170px);
        height: 80px;
        padding-left: 10px;
    }

    .action-data {
        text-align: left;
        margin-top: 35px;

    }

    .action-option {
        margin-top: 5px;
    }
</style>



