<template>
    <el-dialog :title="isEditRule?'': ''" :visible.sync="visible" width="80%">
        <div class="rule-edit-form">
            <!--  -->
            <div class="el-form demo-form-inline el-form--inline conditon">
                <div class="el-form-item" style="margin-bottom:8px;">
                    <label class="el-form-item__label"></label>
                    <div class="el-form-item__content">
                        <el-select v-model="rule.method" style="width: 100px;" size="small" placeholder="">
                            <el-option v-for="item in methodList" :key="item.value" :label="item.label"
                                       :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="el-form-item"
                     style="margin-bottom:8px;width: calc(100% - 170px);padding-left: 20px;margin-right: 0px">
                    <div class="el-form-item__content" style="width: 100%">
                        <el-input v-model="rule.match" style="width: 100%" size="small"
                                  placeholder="urlurl"></el-input>
                    </div>
                </div>
            </div>
            <!--  -->
            <div style="padding: 10px 0;">
                <span style="width: 85%;display: inline-block">
                    <el-input v-model="rule.name" size="small" placeholder=""></el-input>
                </span>
                <span style="width: 10%;display: inline-block">
                    <el-button type="text" @click="addAction"></el-button>
                </span>
            </div>
            <!-- action -->
            <div>
                <div v-for="action,index in rule.actionList" :key="index" class="dashed-border">
                    <span style="width: 85%;display: inline-block">
                        <action-detail :action="action"
                                       :rule-types="ruleTypes"
                                       :data-list="dataList"
                                       :user-id="userId"
                                       @new-data-file="$emit('new-data-file', index)"
                                       @edit-data-file="$emit('edit-data-file', $event)"></action-detail>
                    </span>
                    <span style="width: 10%;display: inline-block;vertical-align: bottom;line-height: 107px;height: 107px;">
                        <el-button type="text" @click="deleteAction(index)"></el-button>
                    </span>
                </div>
            </div>
            <!--  -->
            <div style="text-align: right;">
                <el-button @click="cancelEdit"></el-button>
                <el-button type="primary" @click="saveRule">{{isEditRule?'': ''}}</el-button>
            </div>
        </div>
    </el-dialog>
</template>

<script>
  import ActionDetail from './ActionDetail.vue';
  import uuidV4  from 'uuid/v4';
  import './index.css'

  const DefaultRule = {
    name: "",
    key: '',
    method: "",
    match: "",
    checked: false,
    actionList: []
  };
  const DefaultAction = {
    type: "redirect",// redirect  api data
    data: {
      target: "",// 
      dataId: '', //id
      modifyResponseType: '',// 
      callbackName: "", // jsonp
      cookieKey: "", // cookie key
      cookieValue: "", // cookie value
      reqHeaderKey: "", // header
      reqHeaderValue: "",
      resHeaderKey: "", // header
      resHeaderValue: "",
      queryKey: "", // query
      queryValue: "",
      modifyRequestScript: "", // 
      modifyResponseScript: "" // 
    }
  };
  export default {
    name: "RuleEditForm",
    props: ['dataList', 'userId'],
    components: {
      [ActionDetail.name]: ActionDetail
    },
    data() {
      return {
        visible: false,
        isFilterRule: false, // 
        isEditRule: false, // 
        rule: JSON.parse(JSON.stringify(DefaultRule)),
        ruleIndex: -1, // rule 
        methodList: [
          {value: '', label: ''},
          {value: 'get', label: 'GET'},
          {value: 'post', label: 'POST'},
          {value: 'put', label: 'PUT'},
          {value: 'patch', label: 'PATCH'},
          {value: 'delete', label: 'DELETE'},
          {value: 'option', label: 'OPTION'}
        ]
      }
    },

    computed: {
      ruleTypes() {
        if (this.isFilterRule) {
          return [
            //  { value: 'redirect', label: '' },
            //  { value: 'mockData', label: '' },
            {value: 'addRequestHeader', label: ''},
            {value: 'addQuery', label: 'Query'},
            {value: 'addResponseHeader', label: ''},
            //  { value: 'modifyResponse', label: '' },
            {value: 'addRequestCookie', label: 'cookie'},
            {value: 'scriptModifyRequest', label: 'js'},
            {value: 'scriptModifyResponse', label: 'js'}
          ]
        } else {
          return [
            {value: 'redirect', label: ''},
            {value: 'mockData', label: ''},
            {value: 'addRequestHeader', label: ''},
            {value: 'addQuery', label: 'Query'},
            {value: 'addResponseHeader', label: ''},
            {value: 'modifyResponse', label: ''},
            {value: 'addRequestCookie', label: 'cookie'},
            {value: 'scriptModifyRequest', label: 'js'},
            {value: 'scriptModifyResponse', label: 'js'}
          ]
        }
      },
    },
    methods: {
      cancelEdit() {
        this.visible = false;
      },

      saveRule() {
        this.$emit('save', {
          isEditRule: this.isEditRule,
          ruleIndex: this.ruleIndex,
          rule: JSON.parse(JSON.stringify(this.rule))
        });
        this.visible = false;
      },

      editRule({
                 rule, isFilterRule, ruleIndex
               }) {
        this.isEditRule = true;
        this.ruleIndex = ruleIndex;
        this.isFilterRule = isFilterRule;
        this.rule = JSON.parse(JSON.stringify(rule));
        this.visible = true;
      },

      createRule({
                   initialRule,
                   isFilterRule = false
                 }) {
        this.ruleIndex = -1;
        this.isEditRule = false;
        this.isFilterRule = isFilterRule;
        let rule;

        if (initialRule) {
          rule = JSON.parse(JSON.stringify(initialRule));
        } else {
          rule = JSON.parse(JSON.stringify(DefaultRule));
          let action = JSON.parse(JSON.stringify(DefaultAction));
          action.type = this.isFilterRule ? "addRequestHeader" : "redirect";// redirect
          rule.actionList.push(action);
        }

        rule.key = uuidV4();

        this.rule = rule;
        this.visible = true;
      },

      addAction() {
        let action = JSON.parse(JSON.stringify(DefaultAction));
        action.type = this.isFilterRule ? "addRequestHeader" : "redirect";// redirect
        this.rule.actionList.push(action);
      },

      deleteAction(index) {
        this.rule.actionList.splice(index, 1);
      },

      setActionDataFileId(actionIndex, dataId) {
        this.rule.actionList[actionIndex].data.dataId = dataId;
      }
    }
  }
</script>




