<template>
    <div>
        <div class="main-content__title">{{ loaded ? ': ' + filecontent.name: '' }}</div>
        <div class="project-path-info"></div>
        <el-row :gutter="20" style="margin-bottom: 10px;text-align: right;">
            <el-col :span="6" :offset="18">
                <el-button size="small" @click='addRule' :disabled='filecontent.meta.remote'></el-button>
                <el-button size="small" type="primary" @click='saveFileRightNow'></el-button>
            </el-col>
        </el-row>
        <el-table border style="width: 100%" row-key="key" :stripe="true" align='center' :data="filecontent.content">
            <el-table-column prop="checked" label="" align="center" width="80">
                <template scope='scope'>
                    <el-tooltip class="item" effect="dark" content="" placement="left">
                        <el-checkbox v-model="scope.row.checked"></el-checkbox>
                    </el-tooltip>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template scope='scope'>
                    <rule-detail :rule="scope.row" :remote="filecontent.meta.remote"></rule-detail>
                </template>
            </el-table-column>
            <el-table-column label="" :width="100" align="center" :context="_self">
                <template scope='scope'>
                    <div class="action-panel" v-if="!filecontent.meta.remote">
                        <div class="btn-panel">
                            <el-tooltip class="item" effect="dark" content="" placement="left">
                                <el-button icon='el-icon-document' size="mini"
                                           @click='onDuplicateRow(scope.row,scope.$index,filecontent.content)'>
                                </el-button>
                            </el-tooltip>
                        </div>
                        <div class="btn-panel">
                            <el-tooltip class="item" effect="dark" content="" placement="top">
                                <el-button type="danger" icon='el-icon-delete' size="mini"
                                           @click='onDeleteRow(scope.row,scope.$index,filecontent.content)'>
                                </el-button>
                            </el-tooltip>
                        </div>
                        <div class="btn-panel">
                            <el-tooltip class="item" effect="dark" content="" placement="left">
                                <el-button type="blue" icon='el-icon-search' size="mini"
                                           @click='testMatchRuleRequest(scope.row)'>
                                </el-button>
                            </el-tooltip>
                        </div>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-dialog title="()" v-model="testMatchRuleFormVisible" size="large">
            <el-form :model="testMatchRuleForm" label-width="120px">
                <el-form-item label="url">
                    <el-input v-model="testMatchRuleForm.url"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="testMatchRuleForm.match"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="testMatchRuleForm.targetTpl"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="testMatchRuleForm.matchRlt" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="testMatchRuleForm.targetRlt" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="testMatchRuleForm.msg" type="textarea" :disabled="true"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="testMatchRuleFormVisible = false"> </el-button>
                <el-button type="primary" @click="testMatchRule"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import ruleApi from '../../../api/rule';
    import _ from 'lodash';
    import uuidV4  from 'uuid/v4';
    import RuleDetail from './RuleDetail';
    import Vue from 'vue';
    Vue.component(RuleDetail.name, RuleDetail);

    export default {
        name: 'edit-rule',
        data() {
            return {
                loaded: false,
                name: '',
                filecontent: { meta: {} },
                testMatchRuleFormVisible: false,
                testMatchRuleForm: {
                    url: '',// url
                    match: '',// url
                    targetTpl: '',//  urlRegtargetTpl $1 $2
                    matchRlt: '',// url
                    targetRlt: '',// 
                    msg: ''
                }
            };
        },
        methods: {
            getFile() {
                this.loaded = false;
                this.name = this.$route.query.name;
                ruleApi.getFileContent(this.$route.query.name).then((response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                        this.loaded = true;
                        // key,
                        _.forEach(serverData.data.content, (rule) => {
                            rule.key = rule.key || uuidV4();
                        });

                        if (this.$dc.dataList.length > 0) {
                            this.filecontent = serverData.data;
                        } else {
                            // element select bug optionselectmodel''
                            // 1soption
                            setTimeout(() => {
                                this.filecontent = serverData.data;
                            }, 1000);
                        }

                    } else {
                        this.$message.error(`${serverData.msg}`);
                    }
                }).catch((error) => {
                    //  npm run dev
                    this.filecontent = {
                        meta: {},
                        "content": []
                    };
                });
            },
            onDeleteRow(row, index, list) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.filecontent.content.splice(index, 1);
                });
            },
            onDuplicateRow(row, index, list) {
                var copy = _.cloneDeep(this.filecontent.content[index]);
                this.filecontent.content.splice(index, 0, copy);
            },
            /**
             * 
             */
            saveFileRightNow(){
                this.saveFile();
                ruleApi.debouncedSaveFile.flush();
            },
            saveFile(donotalert) {
                ruleApi.debouncedSaveFile(this.name, this.filecontent, (response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                        if (!donotalert) {
                            this.$message({
                                showClose: true,
                                type: 'success',
                                message: '!'
                            });
                        }
                    } else {
                        this.$message.error(`${serverData.msg}`);
                    }
                });
            },
            addRule() {
                this.filecontent.content.unshift({
                    name: "",
                    key: uuidV4(),
                    method: "",
                    match: "",
                    checked: true,
                    actionList: [{
                        type: "redirect",// redirect  api data
                        data: {
                            target: "",// 
                            dataId: '', //id
                            modifyResponseType: '',// 
                            callbackName: "", // jsonp
                            cookieKey: "", // cookie key
                            cookieValue: "", // cookie value
                            reqHeaderKey: "", // header
                            reqHeaderValue: "",
                            resHeaderKey: "", // header
                            resHeaderValue: "",
                            queryKey: "", // query
                            queryValue: "",
                            modifyRequestScript: "", // 
                            modifyResponseScript: "" // 
                        }
                    }]
                });
            },
            /**
             url: '',// url
             urlMatch: '',// url
             urlReg: '',// url
             targetTpl: '',//  urlRegtargetTpl $1 $2
             matchRlt:'',// url
             targetRlt: ''// 
             * @param row
             */
            testMatchRuleRequest(row){
                this.testMatchRuleForm.match = row.match;
                this.testMatchRuleForm.targetTpl = row.actionList[0] && row.actionList[0].data.target || '';
                this.testMatchRuleForm.url = '';
                this.testMatchRuleForm.matchRlt = '';
                this.testMatchRuleForm.targetRlt = '';
                this.testMatchRuleForm.msg = '';
                this.testMatchRuleFormVisible = true;
            },
            testMatchRule(){
                ruleApi.testRule(this.testMatchRuleForm).then((response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                        this.testMatchRuleForm.matchRlt = serverData.data.matchRlt;
                        this.testMatchRuleForm.targetRlt = serverData.data.targetRlt;
                        this.testMatchRuleForm.msg = serverData.data.msg;
                    }
                });
            }
        },
        mounted() {
            this.getFile();
        },
        watch: {
            '$route'(to, from) {
                this.getFile();
            }
            // 
            /*filecontent: {
             handler(newVal, oldVal){
             if (oldVal.content) {
             this.saveFile(true);
             }
             },
             deep: true
             }*/
        }
    };

</script>
<style>
    .action-panel .btn-panel {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px;
    }
</style>


