<template>
    <div class="project-wraper">
        <div class="main-content__title"></div>
        <div class="project-path-info">httphttp${ helloworld }/dist/hello.js${
            helloworld }helloworld
        </div>
        <el-table
                :data="$dc.redirectPathVariableArray"
                style="width:100%;">
            <el-table-column
                    prop="key"
                    label=""
                    width="250">
                <template slot-scope="scope">
                    <el-input v-model="scope.row.key" placeholder=""/>
                </template>
            </el-table-column>
            <el-table-column
                    prop="value"
                    label="">
                <template slot-scope="scope">
                    <el-input v-model="scope.row.value" placeholder=""/>
                </template>
            </el-table-column>
            <el-table-column
                    width="60"
                    label="">
                <template slot-scope="scope">
                    <el-button
                            class="delete-btn"
                            type="danger"
                            icon='el-icon-delete'
                            size="mini"
                            @click='deleteParam(scope.row,scope.$index)'
                    />
                </template>
            </el-table-column>
        </el-table>
        <div style="margin-top: 50px;text-align: right;">
            <el-button @click="addParam"></el-button>
            <el-button type="primary" @click="saveFile"></el-button>
        </div>
    </div>
</template>

<script>
  import profileApi from '../../../api/profile';
  import './project.pcss'
  import forEach from 'lodash/forEach';

  export default {
    name: 'project-path',

    methods: {
      deleteParam(row, index, list) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$dc.redirectPathVariableArray.splice(index, 1);
        });
      },

      async saveFile() {
        var redirectPathVariableMap = {};
        forEach(this.$dc.redirectPathVariableArray, obj => {
          redirectPathVariableMap[obj.key] = obj.value;
        });
        this.$dc.profile.redirectPathVariables = redirectPathVariableMap;
        let response = await profileApi.saveFile(this.$dc.profile);
        let serverData = response.data;
        if (serverData.code == 0) {
          this.$message({
            showClose: true,
            type: 'success',
            message: '!'
          });
        } else {
          this.$message.error(`${serverData.msg}`);
        }
      },

      addParam() {
        this.$dc.redirectPathVariableArray.push({
          key: '',
          value: ''
        });
      }
    }
  };
</script>



