<template>
  <div class="install-body">
    <h1>Http Trick</h1>
    <h2 id="toc_0"></h2>

    <p>http trickhttp</p>
    <p>http trick openssl  openssl ( 0.9.8 )</p>
    <p>PAC: {{pacUrl}}</p>

    <h2 id="toc_1">chrome ()</h2>

    <p> SwitchyOmega <a
      href="https://chrome.google.com/webstore/detail/proxy-switchyomega/padekgcemlokbadohgkifijomclgjgif?hl=en-US"
      target="_blank"></a></p>

    <h4 id="toc_2"></h4>

    <ol>
      <li><code>127.0.0.1</code>: http<code>http trick</code>(8001)</li>
      <li> SwitchyOmega <a href="/help/chrome/" target="_blank">chrome </a></li>
    </ol>

    <h2 id="toc_3"></h2>

    <h4 id="toc_4">1. </h4>

    <p><code>http trick</code> https <code>http trick</code>https </p>

    <h4 id="toc_5">2. </h4>

    <ol>
      <li>mac <a :href="url"></a></li>
      <li><img class="install-body__qrcode" :src="imgUrl" ></li>
      <li><a href="/help/cert/" target="_blank"></a></li>
    </ol>
  </div>

</template>

<script>
import qrcode from 'qrcode-js';

export default {
    name: 'help-info',
    data() {
      return {};
    },
    computed: {
      url(){
          let certUrl =
              'http://' +
              this.$dc.appInfo.pcIp +
              ':' +
              this.$dc.appInfo.webUiPort +
              '/utils/rootCA.crt';
          return certUrl;
      },
      imgUrl() {
          let certUrl =
              'http://' +
              this.$dc.appInfo.pcIp +
              ':' +
              this.$dc.appInfo.webUiPort +
              '/utils/rootCA.crt';
          return qrcode.toDataURL(certUrl, 4);
      },
      pacUrl() {
        return `http://${this.$dc.appInfo.pcIp}:${this.$dc.appInfo.webUiPort}/profile/${this.$dc.userId}/proxy.pac`
      }
    }
};
</script>

<style>
.install-body {
  margin: -50px;
  padding: 50px;
  font-size: 14px;
  line-height: 1.8;
  background-color: #fff;

  &__qrcode {
    display: block;
    margin-left: -15px;
  }

  h1,
  h2,
  h3,
  h4,
  h5 {
    font-weight: 400;
    margin: 20px 0 10px;
    color: rgba(51, 51, 51, 0.9);
  }

  h1 {
    margin: 0;
    font-size: 32px;
    font-family: Dosis, Source Sans Pro, Helvetica Neue, Arial, sans-serif;
  }

  h2 {
    color: #333;
    font-size: 24px;
    margin-top: 30px;

    &:first-of-type {
      margin-top: 10px;
    }
  }

  h4 {
    font-size: 18px;
  }

  code {
    margin: 2px;
    color: #455a64;
    padding: 2px 7px;
    font-size: 13px;
    overflow-x: auto;
    font-weight: 400;
    line-height: 22px;
    border-radius: 3px;
    margin-bottom: 25px;
    position: relative;
    word-break: break-all;
    white-space: pre-wrap;
    background-color: #f5f7fa;
    font-family: Source Code Pro,Monaco,Inconsolata,monospace;
  }

  ol {
    padding-left: 20px;
  }

  a {
    color: #3498db;
    text-decoration: none;
  }
}
</style>



