<template>
    <div>
        <div class="main-content__title"></div>
        <div class="project-path-info">
            http trick<br/>
            root<br/>
            
        </div>
        <div class="bind-qrcode-panel">
            <div class="title"></div>
            <div class="qrcode">
                <img class="bind-qrcode" :src="imgUrl">
            </div>
            <div class="bottom">
                <el-button type="text" @click="copyBindUrl"></el-button>
            </div>
        </div>
        <el-table border align='center' style="width: 800px;" :data="this.$dc.bindedDeviceList">
            <el-table-column prop="id" label="ID" align="center" width="150" :sortable="true">
            </el-table-column>
            <el-table-column prop="name" label="Name" align="center" :sortable="true">
            </el-table-column>
            <el-table-column label="" :width="136" align="center" :context="_self">
                <template scope='scope'>
                    <el-button type="danger" icon='el-icon-delete' size="mini"
                               @click='unbind(scope.row, scope.$index )'>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import qrcode from 'qrcode-js';
    import copyToClipboard from 'copy-to-clipboard';

    import profileApi from '../../../api/profile';
    import './devicelist.pcss'

    export default {
        name: 'DeviceList',

        methods: {
            copyBindUrl() {
                copyToClipboard(this.bindUrl);
                this.$message('url');
            },
            async unbind(row, index) {
                await profileApi.unBind(row.id);
                this.$message('');
            }
        },

        computed: {
            bindUrl() {
                return `http://${this.$dc.appInfo.pcIp}:${this.$dc.appInfo.webUiPort}/profile/device/bind?userId=${this.$dc.userId}`;
            },
            imgUrl() {
                return qrcode.toDataURL(this.bindUrl, 4);
            }
        }
    };

</script>



